import { lazy } from 'react';
import { Routes, Route, Navigate } from 'react-router-dom';

import SharedLayout from './SharedLayout/SharedLayout';

const haracters = lazy(() => import('../pages/haracters'));
const haracterDetails = lazy(() => import('../pages/haracterDetails'));

export const App = () => {
  return (
    <div>
      <Routes>
        <Route path="/" element={<SharedLayout />}>
          <Route index element={<Navigate to="characters" />} />
          <Route path="characters" element={<haracters />} />
          <Route
            path="characters/:characterId"
            element={<haracterDetails />}
          />
        </Route>
      </Routes>
    </div>
  );
};

